








































































import TooltipButton from './common/TooltipButton.vue';
import NewColumnDialog from './dialogs/NewColumnDialog.vue';
import EditColumnsDialog from './dialogs/EditColumnsDialog.vue';
import NewRowDialog from './dialogs/NewRowDialog.vue';
import EditRowDialog from './dialogs/EditRowDialog.vue';
import TooltipIcon from './common/TooltipIcon.vue';
import Start from './Start.vue';

import { Column, Row } from '@/classes';

import { MUTATIONS as M} from '@/store/mutation-types';
import { Component, Vue } from 'vue-property-decorator';

@Component({
    components: {
        TooltipButton,
        NewColumnDialog,
        EditColumnsDialog,
        NewRowDialog,
        TooltipIcon,
        EditRowDialog,
        Start,
    },
})
export default class Table extends Vue {
    showEditRowDialog = false;
    dense = true;
    itemToEdit = {};
    get isTableInited(): boolean {
        return this.$store.getters.isTableInited; 
    }
    get columns(): Column[] {
        return this.$store.getters.columns;
    }
    get headers() {
        return [...this.columns, {
            text: '',
            value: 'action',
            width: '90',
            sortable: false,
        }];
    }
    get rows(): Row[] {
        return this.$store.getters.rows;
    }
    get items() {
        return this.rows.map(row => ({
            id: row.id,
            name: row.name,
            ...row.values,
            }
        ));
    }
    async deleteItem(item: Row) {
      const confirmed = await this.$confirm(`Czy chcesz usunąć wiersz ${item.name}?`)
      if (confirmed) this.$store.commit(M.deleteRow, item)
    }
    editItem(item: Row) {
        this.showEditRowDialog = true;
        this.itemToEdit = item;
    }
}
